.scene {
  /*border: 1px solid #CCC;*/
  position: relative;
  width: 210px;
  height: 140px;
  margin: 40px auto;
  perspective: 5000px;
}

.carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 1s;
}

.carousel__cell {
  position: absolute;
  width: 190px;
  height: 120px;
  left: 10px;
  top: 10px;
  border: 2px solid black;
  line-height: 35px;
  font-size: 15px;
  /*font-weight: bold;*/
  color: rgb(172, 172, 172);
  text-align: center;
  background: rgba(0, 0, 0, 0.8);
  transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -webkit-transition: opacity 0.5s ease-in-out;
}

.carousel__cell:nth-child(1) {
  transform: rotateX(0deg) translateZ(120px);
}
.carousel__cell:nth-child(2) {
  transform: rotateX(60deg) translateZ(120px);
}
.carousel__cell:nth-child(3) {
  transform: rotateX(120deg) translateZ(120px);
}
.carousel__cell:nth-child(4) {
  transform: rotateX(180deg) translateZ(120px);
}
.carousel__cell:nth-child(5) {
  transform: rotateX(240deg) translateZ(120px);
}
.carousel__cell:nth-child(6) {
  transform: rotateX(300deg) translateZ(120px);
}

/* gradient fallbacks taken from csstricks.com */
body {
  background-color: b;

  /* SVG fallback for IE 9 (could be data URI, or could use filter) 
  background-image: url(fallback-gradient.svg);*/

  /* Safari 4, Chrome 1-9, iOS 3.2-4.3, Android 2.1-3.0 */
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#1d1e22),
    to(#333642)
  );

  /* Safari 5.1, iOS 5.0-6.1, Chrome 10-25, Android 4.0-4.3 */
  background-image: -webkit-linear-gradient(left, #1d1e22, #333642);

  /* Firefox 3.6 - 15 */
  background-image: -moz-linear-gradient(left, #1d1e22, #333642);

  /* Opera 11.1 - 12 */
  background-image: -o-linear-gradient(left, #1d1e22, #333642);

  /* Opera 15+, Chrome 25+, IE 10+, Firefox 16+, Safari 6.1+, iOS 7+, Android 4.4+ */
  background-image: linear-gradient(to right, #1d1e22, #333642);
  margin: 0px;
  /*overflow: hidden;*/
}

