/*********************************
 ************ ANIMATIONS LIGHT BUTTONS *********
 *********************************/

.led-container {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: transparent;
}
.led {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 0px;
  width: 270px;
  margin: 0 auto;
  border-radius: 50%;
  -webkit-animation: blinkYellow 3s infinite;
  -moz-animation: blinkYellow 3s infinite;
  -ms-animation: blinkYellow 3s infinite;
  -o-animation: blinkYellow 3s infinite;
  animation: blinkYellow 3s infinite;
}
.led.top {
  top: 25%;
}
.led.mid {
  top: 50%;
}

.led.bot {
  top: 75%;
}

@-webkit-keyframes blinkYellow {
  from {
    box-shadow: rgba(255, 197, 143) 0 0 100px 30px;
  }
  20% {
