.text {
  background-color: black;
  color: white;
  font-size: 10vw;
  font-weight: bold;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  mix-blend-mode: multiply;
}

div.menu-container div li {
  list-style: none;
  margin: 0 15px;
}

div.menu-container div a.anchor-menu {
  position: relative;
  display: block;
  text-align: center;
  line-height: 60px;
  background: #272424;
  border-radius: 5%;
  font-size: 30px;
  color: rgba(255, 255, 255, 0.5);
  transition: 0.5s;
  text-decoration: none !important;
}

div.menu-container div a.anchor-menu:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(255, 197, 143);
  border-radius: 5%;
  transition: 0.5s;
  transform: scale(0.9) !important;
  z-index: -1;
}

div.menu-container div a.anchor-menu:hover:before {
  transform: scale(1.2) !important;
  box-shadow: 0 0 15px rgb(255, 197, 143);
  filter: blur(3px);
}

div.menu-container div li a.anchor-menu:hover {
  color: rgb(252, 148, 52);
  box-shadow: 0 0 15px rgb(255, 197, 143);
  text-shadow: 0 0 15px rgb(255, 197, 143);
}

